body {
  --aqua-color: #00acdc;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo__link {
  height: 40px;
  text-decoration: none;
}

.nav__list {
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding: 0;
  display: flex;
}

.nav__item {
  list-style: none;
}

.nav__item:not(.first-item) {
  margin-left: 20px;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  height: 60px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.is-active {
  color: var(--aqua-color);
}

.is-active:after {
  content: "";
  background-color: var(--aqua-color);
  border-radius: 8px;
  width: auto;
  height: 4px;
  display: block;
  position: relative;
  bottom: 4px;
}

.nav__link:hover {
  color: var(--aqua-color);
}

